@charset "UTF-8";
* {
  font-family: "Microsoft YaHei", "PingFang SC", "MS PGothic", "Hiragino Kaku Gothic ProN", "굴림", gulim, "Apple SD Gothic Neo", sans-serif;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
img,
button,
select {
  margin: 0;
  padding: 0;
}

select {
  outline: none;
}

body {
  background-color: #fff;
  -webkit-text-size-adjust: none;
}

img,
fieldset {
  border: 0;
  vertical-align: top;
}

em,
address {
  font-style: normal;
}

ul,
ol,
li {
  list-style: none;
}

textarea {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: transparent;
  border: 0;
  word-break: keep-all;
  word-wrap: break-word;
}

button {
  appearance: none;
  -webkit-appearance: none;
  background-color: transparent;
  border: 0;
}

.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:visited,
a:active {
  color: #191919;
}

button {
  cursor: pointer;
  border: 0;
  background-color: #fff;
}

button:focus,
button:active {
  outline: none;
}

.dimmed {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 10;
}

.coach_mark {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
}

.coach_mark .coach_mark_box {
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
}

.coach_mark .coach_mark_box .coach_mark_thumb {
  display: table-cell;
  vertical-align: middle;
}

.coach_mark .coach_mark_box p {
  margin-top: 20px;
  font-size: 14px;
  color: #fff;
  letter-spacing: -0.21px;
  line-height: 20px;
}

.coach_mark .coach_mark_box p strong {
  font-weight: 900;
}

.coach_mark .coach_mark_close {
  position: absolute;
  top: 3px;
  left: 15px;
  padding: 12px;
  background-color: transparent;
}

.coach_mark .coach_mark_close:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  background-image: url("../imgs/sp_gift.png");
  width: 14px;
  height: 14px;
  background-position: 0 -63px;
  background-size: 100px 100px;
  -webkit-background-size: 100px 100px;
}

