/* Component */
.component div {
  margin-top: 10px;
}

.default {
  width: 100%;
  height: 48px;
  background-color: #127eff;
  border-radius: 24px;
  font-size: 14px;
  font-weight: 900;
  color: #fff;
  letter-spacing: -.21px;
}

.default.thin {
  height: 40px;
  font-weight: bold;
}

.default:disabled,
.default [disabled] {
  font-weight: bold;
  background-color: #ebecef;
  color: rgba(0, 0, 0, 0.15);
  cursor: not-allowed;
  pointer-events: none;
}

.btn_half {
  display: flex;
  margin: 0 -4px;
}

.btn_half .default {
  width: 50%;
  margin: 0 4px;
  background-color: #fff;
  color: #127eff;
  border: 1px solid #127eff;
}

.press {
  width: auto;
  height: 32px;
  padding: 0 18px;
  background-color: #fff;
  border: 1px solid #127eff;
  border-radius: 50px;
  font-size: 13px;
  font-weight: bold;
  color: #127eff;
  white-space: nowrap;
}

.press.selected {
  background-color: rgba(18, 126, 255, 0.08);
}

.press:disabled,
.press [disabled] {
  border-color: #cbe3ff;
  color: rgba(18, 126, 255, 0.3);
  cursor: not-allowed;
  pointer-events: none;
}

.list_label {
  display: inline-block;
  padding: 3px 6px 1px;
  border-radius: 30px;
  font-size: 9px;
  font-weight: bold;
  color: #fff;
  letter-spacing: -.14px;
  vertical-align: top;
}

.list_label.event {
  background-color: #4d93ff;
}

.list_label.biz {
  background-color: #666cf2;
}

.list_label.dday {
  background-color: #000;
  opacity: .4;
}

.list_label.dday_imminent {
  background-color: rgba(252, 76, 96, 0.87);
}

.list_label.reservation {
  background-color: #0ec1b8;
}

.list_label.reservation:before {
  content: '';
  display: inline-block;
  margin: 0 2px 0 0;
  vertical-align: top;
  background-image: url("../imgs/sp_gift.png");
  width: 10px;
  height: 10px;
  background-position: 0 0;
  background-size: 100px 100px;
  -webkit-background-size: 100px 100px;
}

.detail_label {
  display: inline-block;
  padding: 5px 8px 4px;
  border-radius: 30px;
  font-size: 11px;
  font-weight: bold;
  color: #fff;
  letter-spacing: -.17px;
  vertical-align: top;
}

.detail_label.event {
  background-color: #4d93ff;
}

.detail_label.biz {
  background-color: #666cf2;
}

.toast {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 40px);
  height: 48px;
  border-radius: 8px;
  background-color: #000;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  letter-spacing: -.2px;
  opacity: .75;
  z-index: 20;
}

.product_list {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.product_list + .product_list {
  margin-top: 40px;
}

.product_list li {
  flex: 1;
  margin-left: 15px;
}

.product_list li:first-child {
  margin-left: 0;
}

.product_list li a {
  display: block;
}

.product_list li .thumb {
  overflow: hidden;
  position: relative;
  margin-bottom: 8px;
  border-radius: 12px;
  background-color: transparent;
}

.product_list li .thumb .label_box {
  display: block;
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.7);
  z-index: 10;
}

.product_list li .thumb .label_box .state_label {
  position: absolute;
  bottom: 10px;
  right: 10px;
  display: block;
  width: 44px;
  height: 44px;
  border: 2px solid;
  border-color: rgba(0, 0, 0, 0.38);
  border-radius: 50%;
  box-sizing: border-box;
  padding: 7px 0;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: -0.16px;
  color: rgba(0, 0, 0, 0.87);
  text-align: center;
}

.product_list li .thumb .label_box .state_label.refund {
  border-color: rgba(252, 76, 96, 0.87);
}

.product_list li .thumb .list_label.event {
  position: absolute;
  top: 10px;
  left: 10px;
}

.product_list li .thumb .list_label.biz {
  position: absolute;
  top: 10px;
  left: 10px;
}

.product_list li .thumb .list_label.dday, .product_list li .thumb .list_label.dday_imminent, .product_list li .thumb .list_label.reservation {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.product strong,
.product span {
  display: block;
}

.product .product_distributor {
  overflow: hidden;
  font-size: 11px;
  color: rgba(0, 0, 0, 0.38);
  letter-spacing: -.17px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.product .product_title {
  overflow: hidden;
  margin-top: 4px;
  font-size: 13px;
  font-weight: 900;
  color: rgba(0, 0, 0, 0.87);
  letter-spacing: -.2px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.product .from_present {
  overflow: hidden;
  margin-top: 8px;
  font-size: 11px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.87);
  letter-spacing: -.17px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.product .use_expire {
  overflow: hidden;
  margin-top: 4px;
  font-size: 11px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.38);
  letter-spacing: -.17px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.product .use_expire .division {
  display: inline-block;
  width: 1px;
  height: 10px;
  margin: 2px 4px 0 6px;
  background-color: rgba(0, 0, 0, 0.38);
  vertical-align: top;
}

.detail_thumb {
  display: block;
  overflow: hidden;
  position: relative;
  width: 295px;
  height: 208px;
  margin: 0 auto;
  border-radius: 12px;
  text-align: center;
}

.detail_thumb > img {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  height: 100%;
  min-width: 240px;
  min-height: 170px;
  max-width: 295px;
  max-height: 208px;
}

.detail_thumb .label_box {
  display: block;
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.7);
  z-index: 10;
}

.detail_thumb .label_box .state_label {
  position: absolute;
  bottom: 10px;
  right: 10px;
  display: block;
  width: 60px;
  height: 60px;
  border: 2px solid;
  border-color: rgba(0, 0, 0, 0.38);
  border-radius: 50%;
  box-sizing: border-box;
  padding: 11px 0;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.21px;
  color: rgba(0, 0, 0, 0.87);
  text-align: center;
}

.detail_thumb .detail_label {
  position: absolute;
  top: 12px;
  left: 12px;
}

.message_box {
  display: block;
  padding: 20px 0;
  position: relative;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}

.message_box strong {
  display: block;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: -0.22px;
  color: rgba(0, 0, 0, 0.87);
}

.message_box span {
  display: block;
  margin-top: 12px;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: -0.2px;
  color: rgba(0, 0, 0, 0.6);
}

.message_box span:only-child {
  margin-top: 0;
}

.event_notice {
  display: block;
  border: 1px solid #e5e5e5;
  border-radius: 16px;
  padding: 24px 20px;
  box-sizing: border-box;
  background-color: #fcfcfc;
}

.event_notice > strong {
  display: block;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.21px;
  color: rgba(0, 0, 0, 0.87);
}

.event_notice > p {
  display: block;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.18px;
  color: rgba(0, 0, 0, 0.5);
}

.section_title {
  display: flex;
  width: 100%;
  height: 54px;
  align-items: center;
}

.section_title p {
  display: block;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.24px;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 700;
}

.section_title p em {
  color: #127eff;
}

.section_title .button_appointment {
  margin-left: auto;
  height: 30px;
  padding: 0 12px;
  border: 1px solid #0ec1b8;
  border-radius: 30px;
  font-size: 12px;
  font-weight: bold;
  color: #0ec1b8;
  letter-spacing: -.18px;
}

.section_title .button_appointment:before {
  content: '';
  display: inline-block;
  margin-right: 5px;
  vertical-align: top;
  background-image: url("../imgs/sp_gift.png");
  width: 13px;
  height: 14px;
  background-position: -24px -19px;
  background-size: 100px 100px;
  -webkit-background-size: 100px 100px;
}

.common_box {
  padding: 0 20px;
}

@media all and (min-width: 320px) {
  .container .content .refund_account .account_box .in_box .notice {
    letter-spacing: -.7px !important;
  }
  .container .content .help_desk_info p {
    letter-spacing: -.5px;
  }
  .container .content .section_title .button_appointment {
    letter-spacing: -.7px;
  }
}

/* 팝업 (arert) */
.mob_alert_wrap .sp_lux {
  display: inline-block;
  overflow: hidden;
  background: url(https://static.wehagov.com/imgs/common/sp_lux.png) 0 0 no-repeat;
  line-height: 100em;
  vertical-align: top;
}

.mob_alert_wrap .alert_bx {
  position: relative;
  padding: 40px 16px 30px;
  max-width: 268px;
  background: #fff;
  border-radius: 14px;
  box-sizing: border-box;
}

.mob_alert_wrap .alert_bx .btn_close {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 24px;
  height: 24px;
  border: none;
  background: none;
}

.mob_alert_wrap .alert_bx .btn_close .sp_lux {
  width: 14px;
  height: 14px;
  background-position: -279px -54px;
}

.mob_alert_wrap .alert_bx .title_area {
  text-align: center;
}

.mob_alert_wrap .alert_bx .title_area > img {
  display: inline-block;
  margin-bottom: 30px;
}

.mob_alert_wrap .alert_bx .title_area > strong {
  display: block;
  font-size: 16px;
  font-weight: bold;
  line-height: 19px;
  color: rgba(0, 0, 0, 0.87);
}

.mob_alert_wrap .alert_bx .cont_area {
  margin-top: 18px;
  text-align: center;
  font-size: 13px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.6);
}

.mob_alert_wrap .alert_bx .btn_area {
  margin-top: 30px;
}

.mob_alert_wrap .alert_bx .btn_area .btn_alert + .btn_alert {
