}

.mob_alert_wrap .alert_bx .btn_area .btn_alert {
  display: block;
  width: 100%;
  height: 48px;
  border: none;
  background: #127eff;
  border-radius: 24px;
  font-size: 14px;
  font-weight: bold;
  line-height: 21px;
  color: #fff;
}

.mob_alert_wrap .alert_bx .btn_area .btn_alert.v2 {
  background: #ebecef;
  color: rgba(0, 0, 0, 0.87);
}

.service_list {
  display: flex;
  position: relative;
  height: 46px;
  padding: 16px 0 0;
  background-color: #fff;
}

.service_list:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: -20px;
  right: -20px;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.06);
}

.service_list li {
  position: relative;
}

.service_list li a {
  padding: 0 2px 9px;
  font-size: 16px;
  font-weight: 900;
  color: rgba(0, 0, 0, 0.87);
  letter-spacing: -0.24px;
}

.service_list li.active a {
  position: relative;
}

.service_list li.active a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 3px solid #000;
